<template>
  <div id="swipebox">
    <van-swipe :autoplay="3000">
      <van-swipe-item v-for="data in images" :key="data.bannerId">
        <img v-lazy="data.imgUrl" @click="goDetail(data.actionData)" />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import Vue from "vue";
import { Lazyload, Swipe, SwipeItem } from "vant";

Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Lazyload);
export default {
  name: "Swipe",
  data() {
    return {
      images: [],
    };
  },
  methods: {
    goDetail(actionData) {
      console.log(11, actionData);
      var add = JSON.parse(actionData)
      console.log(add.businessId);
      var busId = JSON.parse(add.businessId)
      this.$router.push('/movie/detail/' + busId);
    },
  },
  mounted() {
    this.axios({
      url: "https://m.maizuo.com/gateway?type=2&cityId=410100&k=9988481",
      headers: {
        "X-Client-Info":
          '{"a":"3000","ch":"1002","v":"5.0.4","e":"16348321693295459686744065","bc":"410100"}',
        "X-Host": "mall.cfg.common-banner",
      },
    }).then((res) => {
      console.log(res.data);
      this.images = res.data.data;
    });
  },
};
</script>

<style scoped>
.van-swipe {
  height: 56vw;
}
img {
  width: 100%;
}
</style>